export default {
    data(){
        return{
            bounce: 'none'
        }
    },
    watch:{
        show(newvalue,oldvalue){
            if(newvalue==true){
                this.bounce = 'show';
            }
            if(newvalue==false){
                if(this.type == 'bottom'){
                    this.bounce = 'hide'
                    setTimeout(()=>{
                        this.bounce = 'none'
                    },200)
                }else{
                    this.bounce = 'none'
                }
            }
        }
    },
    props:{
        // 显示隐藏
        show:{
            type: [String,Boolean],
            default: false
        },
        type:{
            type: String,
            default: 'center'
        },
        boxWidth:{
            type: [String,Number],
            default: 'auto'
        },
        boxHeight:{
            type: [String,Number],
            default: 'auto'
        }
    },
    methods:{
        Popup(){
            this.$emit('hidePopup')
        },
        moveHandle() { //阻止遮罩下方的页面滚动
        },
        
    }
}
